<template>
    <div class="main__week-item">
        <h2 class="main__week-item-day">{{ getWeekDay }}</h2>
        <h2 class="main__week-item-date">{{ getDay }} {{ getMonth }}</h2>
        <img :src="getImg" alt="" class="main__week-item-img">
        <p class="main__week-item-temp">{{ plus }}{{ Math.round(day.temp.max) }}°</p>
        <p class="main__week-item-info">{{ day.weather[0].description }}</p>
    </div>
</template>

<script>
import { weatherName } from '@/icon'
import unix from '@/unix'
    export default {
        props: {
            day: {
                type: Object
            }
        },
        computed: {
            plus() {
                if(this.day.temp.max >= 0) {
                    return '+'
                }
            },
            getImg() {
                return weatherName[this.day.weather[0].description] || weatherName['ясно']
            },
            getWeekDay() {
                return unix(this.day.dt, 'weekday')
            },
            getDay() {
                return unix(this.day.dt, 'day')
            },
            getMonth() {
                return unix(this.day.dt, 'month')
            }
        }
    }
</script>

